* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased; }

[ng-cloak] {
  display: none; }

html, body {
  width: 100%;
  height: 100%;
  margin: 0; }

body {
  color: #111;
  -webkit-font-smoothing: antialiased;
  perspective: 1000px;
  line-height: 1.75; }

.container {
  width: 1200px;
  margin: 0 auto; }

.light {
  font-weight: 200; }

.site-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9998;
  background: #8ABEAF;
  border-bottom: 1px solid #000; }
  .site-nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    height: 3rem;
    padding: 0 1em;
    align-items: stretch;
    flex-direction: row; }
  .site-nav li {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center; }
    .site-nav li:last-child {
      margin-left: auto; }
      .site-nav li:last-child a {
        background: #F1AAB4;
        color: #000; }
        .site-nav li:last-child a:hover {
          background: #f5c0c7; }
        .site-nav li:last-child a:active {
          background: #ef9da8; }
  .site-nav a {
    flex: 1 1 auto;
    padding: 0.2em 0.8em;
    color: #000;
