.site-header {
  width: 100vw;
  height: 100vh;
  position: relative;
  background-size: 500px auto;
  background-color: #234246;
  overflow: hidden;
  display: flex;
  perspective: 0px; }

@keyframes move-mask {
  from {
    background-position: 3vw 3vw, 0 0, center center; }
  to {
    background-position: 3vw 3vw, -10000px 5000px, center center; } }
  .site-header .sky {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: url("../images/background/moon.svg"), url("../images/background/mask.png"), url("../images/background/stars.png");
    background-repeat: no-repeat, repeat, repeat;
    background-size: 10vw 10vw, auto auto, 500px auto;
    background-position: 3vw calc(3vw + 3rem), 0 0, center center;
    animation: move-mask 50s linear alternate; }
  .site-header h1 {
    font-family: 'Rockwell W01';
    font-weight: 800;
    text-transform: uppercase;
    align-self: center;
    text-align: right;
    width: 80%;
    margin: 0 auto;
    display: inline-block;
    font-size: 14vw;
    padding-bottom: 0.25em;
    margin-bottom: 1em;
    color: #8ABEAF;
    line-height: 0.9;
    background: url("../images/and.svg") no-repeat 6% bottom;
    background-size: 40% auto;
    position: relative;
    z-index: 1; }
  .site-header .background, .site-header .background > div {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-size: 100% auto;
    z-index: 2;
    transform-style: preserve-3d;
    transform: translateZ(0); }
  .site-header .b7 {
    background-image: url("../images/background/7.svg");
    z-index: 7; }
  .site-header .b6 {
    background-image: url("../images/background/6.svg");
    z-index: 6; }
  .site-header .b5 {
    background-image: url("../images/background/5.svg");
    z-index: 5; }
  .site-header .b4 {
    background-image: url("../images/background/4.svg");
    z-index: 4; }
  .site-header .b3 {
    background-image: url("../images/background/3.svg");
    z-index: 3; }
  .site-header .b2 {
    background-image: url("../images/background/2.svg");
    z-index: 2; }
  .site-header .b1 {
    background-image: url("../images/background/1.svg");
    z-index: 1; }

section {
  font-family: 'Futura W01';
  padding: 100px;
  overflow: hidden; }
  section h1 {
    font-size: 48px;
    text-align: center;
    padding-bottom: 0.4em;
    margin-bottom: 1em;
    position: relative;
    font-weight: 900; }
    section h1:after {
      content: '';
      display: block;
      width: 2em;
      height: 2px;
      background: rgba(255, 255, 255, 0.4);
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto; }

