      .registry .registry-links li:nth-child(4) .name {
        background-image: url("../images/Bed_Bath__and__Beyond.svg"); }

.rsvp {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  padding: 0; }
  .rsvp .overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 1; }
  .rsvp form {
    background: #F1AAB4;
    width: calc(400px + 4em);
    max-width: 100%;
    max-height: calc(100% - 1em);
    overflow: visible;
    margin: auto;
    overflow-x: hidden;
    overflow-y: auto;
    white-space: nowrap;
    transition: 1s all;
    font-size: 20px;
    padding: 2em;
    border-bottom: 1px solid #000;
    border-radius: 0 0 10px 10px; }
    .rsvp form:after {
      content: '';
      display: table;
      clear: both; }
  .rsvp .main-info, .rsvp .additional-info {
    display: inline-block;
    vertical-align: top;
    white-space: normal;
    width: 400px; }
    .rsvp .main-info:after, .rsvp .additional-info:after {
      content: '';
      display: table;
      clear: both; }
  .rsvp .main-info {
    font-size: 1.3em;
    margin-right: -0.32em; }
  .rsvp h1 {
    margin-top: 0; }
  .rsvp label {
    font-size: 1em;
    display: block;
    margin-top: 1em; }
    .rsvp label:first-child {
      margin-top: 0; }
  .rsvp input[type="text"], .rsvp textarea {
    width: 100%;
    appearance: none;
    border: none;
    font-size: 1em;
    padding: 0.3em;
    margin-top: 0.4em;
    display: block;
    background: rgba(255, 255, 255, 0.4);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.4);
    transition: 0.2s box-shadow; }
    .rsvp input[type="text"]:focus, .rsvp textarea:focus {
      box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.4);
      outline: none; }
  .rsvp textarea {
    height: 4em; }
  .rsvp .rsvp-yes-no {
    margin: 2em 0; }
    .rsvp .rsvp-yes-no label {
      display: block;
      margin: 0.5em 0; }
      .rsvp .rsvp-yes-no label:before {
        content: '\2713';
        font-size: 2em;
        color: transparent;
        display: inline-block;
        vertical-align: middle;
        width: 0.6em;
        height: 0.6em;
        line-height: 0.5;
        background: rgba(255, 255, 255, 0.4);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.4);
        transition: 0.2s box-shadow, 0.2s color;
        position: relative;
        top: -0.07em;
        margin-right: 0.5em; }
    .rsvp .rsvp-yes-no input[type="radio"] {
      opacity: 0;
      position: absolute; }
      .rsvp .rsvp-yes-no input[type="radio"]:checked + label:before {
        color: black; }
      .rsvp .rsvp-yes-no input[type="radio"]:focus + label:before {
        box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.4); }
  .rsvp button {
    appearance: none;
    border: none;
    font-size: 1em;
    background: rgba(255, 255, 255, 0.5);
    padding: 0.45em 0.75em;
    float: right;
    cursor: pointer;
    transition: 0.2s background; }
    .rsvp button:hover {
      background: rgba(255, 255, 255, 0.8); }
  .rsvp .additional-info {
    opacity: 1;
    margin-left: 3em;
    position: relative; }
    .rsvp .additional-info.ng-hide-remove, .rsvp .additional-info.ng-hide-add {
      transition: opacity 0.3s; }
    .rsvp .additional-info.ng-hide {
      opacity: 0; }
    .rsvp .additional-info:before {
      content: '';
      position: absolute;
      top: 1em;
      bottom: 1em;
      left: calc(-1.5em - 1px);
      width: 2px;
      background: rgba(0, 0, 0, 0.3); }
  .rsvp label[for="name"] {
    display: block; }
  .rsvp form.rsvp-yes {
    width: calc(800px + 7em); }
  .rsvp.ng-hide-remove {
    transition: all 0.5s; }
    .rsvp.ng-hide-remove .overlay {
      transition: inherit; }
    .rsvp.ng-hide-remove form {
      transition: all 0.25s;
      transition-delay: 0.2s; }
  .rsvp.ng-hide-add {
    transition: all 0.3s; }
    .rsvp.ng-hide-add .overlay, .rsvp.ng-hide-add form {
      transition: inherit; }
    .rsvp.ng-hide-add form {
      transition: all 0.2s; }
  .rsvp.ng-hide .overlay {
    opacity: 0; }
  .rsvp.ng-hide form {
    transform: translate3d(0, -100%, 0); }
